<template>
  <form @submit.prevent="onFormSubmit">
    <page-title/>

    <div class="row">

      <div class="col-sm p-2">
        <div class="panel h-100">
          <div>
            <label class="form-label">عنوان</label>
            <input type="text" class="form-control" v-model="model.pstTitle"/>
          </div>

          <div class="mt-3">
            <label class="form-label">توضیح مختصر</label>
            <textarea class="form-control" v-model="model.pstDescription"></textarea>
          </div>

          <div class="mt-3">
            <label class="form-label">متن</label>
            <textarea id="content-editor" class="form-control" v-model="model.pstContent"></textarea>
          </div>

          <div class="mt-3">
            <label class="form-label">تصویر</label>
            
          </div>
        </div>
      </div>

      <div class="col-sm-4 p-2">

        <div class="panel h-100">
          <categories-check-list v-model="model.postCategory"/>
        </div>
      </div>

    </div>

    <div class="mt-4 d-flex align-items-center">
      <button type="submit" v-if="!working" class="btn btn-success">ثبت محتوا</button>
    </div>

    <input type="file" class="d-none" accept="image/svg+xml,image/jpeg,image/png" ref="fileInput"/>
  </form>
</template>

<script>
import tinymce from '../../library/mixins/tinymce'
import PageTitle from '../../tools/PageTitle'
import { dispatchNotification } from '../../library/stores/notifications'
import { nextTick } from 'vue'
import CategoriesCheckList from './categories/CategoriesCheckList'
import { serverUrl } from '../../library/plugins/AJAX'

export default {
  name: 'PostForm',
  props: {
    pstId: { default: undefined }
  },
  components: {
    CategoriesCheckList,
    PageTitle
  },
  mixins: [
    tinymce
  ],
  data: () => ({
    working: false,
    fileUploading: false,
    model: {
      postCategory: [],
      meta: []
    },
    errors: {}
  }),
  methods: {
    onFormSubmit() {
      if (this.working) {
        return
      }

      this.working = true
      this.errors = {}

      this.$fetch('/post/' + (this.model.pstId ? '' : 'PostCreateUsingCategoryAndType'),
          this.model.pstId ? 'patch' : 'post', {
            ...this.model,
            pstContent: this.getContent('content-editor')
          })
          .then(() => {
            this.$router.push('/cms/posts')
            dispatchNotification('محتوا با موفقیت ثبت شد')
          })
          .finally(() => this.working = false)
    },
    loadData() {
      this.working = true
      this.$fetch(`/post/${this.pstId}`, 'get')
          .then(x => {
            this.model = {
              pstId: x.data.pstId,
              pstTitle: x.data.pstTitle,
              pstContent: x.data.pstContent,
              pstDescription: x.data.pstDescription,
              postCategory: x.data.postCategoryTable.map(x => x.postCategoryId)
            }
            nextTick(() => {
              this.setContent('content-editor', x.data.pstContent)
            })
          })
          .catch(x => console.log(x.responseJSON))
          .finally(() => this.working = false)
    },
    uploadFile() {
      return new Promise(((resolve, reject) => {
        this.$refs.fileInput.onchange = () => {
          if (this.$refs.fileInput.files.length > 0) {
            this.fileUploading = true
            const fd = new FormData()
            fd.append('file', this.$refs.fileInput.files[0])
            this.$fetch('/FileProvider/uploader', 'post', fd)
                .then(x => resolve(x.data))
                .catch(x => reject(x))
                .finally(() => this.fileUploading = false)
          }
        }
        this.$refs.fileInput.click()
      }))
    }
  },
  mounted() {
    this.tinymceLoad('content-editor', (editor) => {
      this.uploadFile()
          .then(x => {
            const img = serverUrl(x.substring(1))
            editor.setContent(`<img src="${img}" style="width : 250px" />`)
          })
    })

    if (this.pstId) {
      this.loadData()
    }
  }
}
</script>

